.header{width:100vw;border-bottom:1px solid #e2e6e9}@media(min-width: 640px){.header{display:flex;gap:16px}}@media(min-width: 1440px){.header{gap:24px}}.header__top{padding-left:16px;height:48px;display:flex;align-items:center}@media(min-width: 1440px){.header__top{padding-left:24px;height:64px}}.header__nav{display:none}@media(min-width: 640px){.header__nav{display:flex;align-items:center;padding:0 16px;gap:32px}}@media(min-width: 1440px){.header__nav{padding:0 24px;gap:64px}}.header__link{font-family:Mont;font-size:12px;line-height:48px;font-weight:700;letter-spacing:.48px;text-transform:uppercase;color:#89939a;transition:color .3s ease-in}.header__link:hover{color:#313237}@media(min-width: 1440px){.header__link{line-height:64px}}.header__sub-nav{display:none;height:48px}@media(min-width: 640px){.header__sub-nav{display:block;margin-left:auto}}@media(min-width: 1440px){.header__sub-nav{height:64px}}.header__ico-link{display:inline-block;width:48px;height:100%;border-left:1px solid #e2e6e9;position:relative}@media(min-width: 1440px){.header__ico-link{width:64px}}.header__ico-link::before{content:"";font-family:"icomoon";font-size:16px;color:#313237;display:flex;align-items:center;justify-content:center;position:absolute;width:100%;height:100%;left:0;top:0}.header__ico-link--favs::before{content:""}.header__ico-link--cart::before{content:""}.header .is-active{color:#313237;position:relative}.header .is-active::before{color:#313237}.header .is-active::after{content:"";display:block;background-color:#313237;width:100%;height:3px;position:absolute;left:0;bottom:0}